@import "../utilities/functions";

@each $color in map-keys($swatch-colors) {
    $colorValue: #{map-get($swatch-colors, $color)};

    .swatch-#{$color} {
        background-color: $colorValue;
        display: block;
        position: relative;

        @if $color == "white" {
            border: 1px solid $gray-700;
        }

        &.disabled {
            opacity: 0.2;
            overflow: hidden;

            &::before {
                border-bottom: 1px solid $gray-300;
                content: "";
                height: 1px;
                position: absolute;
                transform: rotate(45deg) translateX(6px) translateY(14.5px);
                width: sqrt(pow(30, 2) + pow(30, 2)) * 1px;
            }
        }
    }

    .swatch-filter-#{$color} {
        background-color: $colorValue;
        display: block;
        position: relative;

        @if $color == "white" {
            border: 1px solid $gray-700;
        }

        &.disabled {
            opacity: 0.2;
        }
    }
}

.swatch-filter-miscellaneous,
.swatch-miscellaneous {
    background: url(../images/rainbow.jpg);
    background-size: 40px;
    display: block;

    &.disabled {
        opacity: 0.2;
    }

    &:hover {
        animation: move 28s infinite linear forwards;
    }
}

@keyframes move {
    100% {
        background-position: 3000px 0;
    }
}

.swatch-color {
    height: 24px;
    position: relative;
    width: 24px;

    &::before {
        content: "";
