/* -- Main body style -- */


.suggestions-wrapper .phrases .term{
    display: inline !important;
}


.btn-primary{
    color: #ffffff;
}

.hero-banner .btn {
    border-radius: $btn-border-radius;
}


.page .main-content {
    max-width: none;
    margin: 0;
}

.page[data-action=Product-Show]{
    .main-content{
    padding-bottom: 0;
    }
}


.page[data-action=Search-Show]{
    .hero, .hero-banner .banner-image { 
        display: none;
        }
}

.hero-banner .banner-image {
    height: 450px;
}

/* -- Header Nav style -- */

.navbar-nav > li, .site-header .nav-item, .site-header .preheader a, .site-header .preheader .country-selector, .site-header .preheader .minicart-total {
    text-transform: none;
}

.navbar-nav > li {
    font-weight: 500;
    margin-top: 3px;
}

.for-b2b{
    img{
    width: 20px;
    height: auto;
    display: inline-block;
    margin-top: -3px;
    margin-right: 0.2rem ;
    }
    margin-right: 1.5rem !important;
}

.wishlist{
    display: none;
}


/* -- Footer style -- */

.site-footer__header, .site-footer__menu-header {
    font-size: 0.85rem;
    text-transform: none;
}
.site-footer .notice {
    padding: 0 1rem;
    background-color: #5e657a;
}
a.site-footer__menu-link {
    padding-top: 0.5rem;
}



.hero-banner .hero-banner-overlay-wrapper {
    .hero-banner-overlay.container{
        max-width: 1140px;
        h1{
            font-weight: 700;
        }
    }
}

/* -- Product Grid style -- */


.product {
    margin-right: 1em;
}

.product .tile-body .pdp-link .link{
    padding:0 1rem;
}


.product-detail .product-number {
    color: $gray-500 !important;
    font-size: 11px;
    letter-spacing: 3px;
}

.price{
    color: $primary;
}


///remove when ditto is updated
.consent-tracking{
    display: none !important;
}