*:focus {
  outline: none !important;
}

html,
body {
  height: 100%;
}

.checkout,
.page {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow: hidden;
}

.site-footer {
  margin-top: auto;
}

a {
  display: inline-block;
}

button.disabled,
a.disabled {
  cursor: not-allowed;
}
button::disabled,
a::disabled {
  cursor: not-allowed;
}

.img-fluid {
  width: 100%;
}

.main-content {
  overflow-x: hidden;
  padding-bottom: 3rem;
}

@media (min-width: 992px) {
  .service-cloud-agent .main-content {
    padding-top: 153px;
  }

  .main-content {
    padding-top: 113px;
  }
}
@media (max-width: 991.98px) {
  .service-cloud-agent .main-content {
    padding-top: 90px;
  }

  .main-content {
    padding-top: 50px;
  }
}
a {
  transition: opacity 0.2s ease-out;
}
a:hover {
  text-decoration: none;
}

dl {
  margin: 0;
  overflow: hidden;
  padding: 0;
  width: 100%;
}

dt {
  clear: both;
  float: left;
  font-weight: 600;
  margin-right: 5px;
  padding: 0 0.3rem 0 0;
}

dd {
  float: left;
  margin-bottom: 0.25rem;
}

.section-header {
  text-align: center;
  margin-bottom: 3rem;
}

.modal-background {
  background-color: rgba(0, 0, 0, 0.75);
  bottom: 0;
  content: "";
  display: block;
  left: 0;
  opacity: 0;
  position: fixed;
  right: 0;
  top: 0;
  transition: opacity 0.2s, visibility 0ms linear 0.2s, z-index 0ms linear 0.2s;
  visibility: hidden;
  z-index: -1;
}
.modal-background.open {
  opacity: 1;
  transition: opacity 0.2s, visibility 0ms;
  visibility: visible;
  z-index: 2;
}

.container_sm {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}
@media (min-width: 576px) {
  .container_sm {
    max-width: 540px;
  }
}
@media (min-width: 768px) {
  .container_sm {
    max-width: 720px;
  }
}
@media (min-width: 992px) {
  .container_sm {
    max-width: 960px;
  }
}
@media (min-width: 1200px) {
  .container_sm {
    max-width: 1140px;
  }
}

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}
.slick-list:focus {
  outline: none;
}
.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}
.slick-track:after {
  clear: both;
}
.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}
[dir=rtl] .slick-slide {
  float: right;
}
.slick-slide img {
  display: block;
}
.slick-slide.slick-loading img {
  display: none;
}
.slick-slide.dragging img {
  pointer-events: none;
}
.slick-initialized .slick-slide {
  display: block;
}
.slick-loading .slick-slide {
  visibility: hidden;
}
.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

.product-carousel,
.einstein-carousel,
.carousel {
  height: 1px;
  visibility: hidden;
}
.product-carousel.slick-initialized,
.einstein-carousel.slick-initialized,
.carousel.slick-initialized {
  height: auto;
  visibility: visible;
}

.einstein-carousel,
.carousel {
  width: 100%;
}
.einstein-carousel .product,
.carousel .product {
  margin-left: 1rem;
  margin-right: 1rem;
}

.product-carousel.slick-slider,
.banner-carousel.slick-slider,
.einstein-carousel.slick-slider,
.carousel.slick-slider {
  width: 100%;
}
.product-carousel .slick-prev,
.product-carousel .slick-next,
.banner-carousel .slick-prev,
.banner-carousel .slick-next,
